.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5

    .titulo-principal
      .titulo-principal__numero
        .h3 1
      .h3 Produccin pecuaria sostenible

    img.img-a.mb-5(src="@/assets/curso/tema_1/img_1.png")

    p.text_tamano.mb-5 #[strong.color_text La finalidad de una explotacin pecuaria es producir alimentos y/o subproductos de origen animal,] 
      | #[strong con destino a suplir la demanda creciente de la poblacin mundial.] 

    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/2L91WMqw96A" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video: Leyenda del video

    p.mb-3 Para que una produccin pecuaria sea sostenible, lo primero que debe hacer es #[strong contribuir con la preservacin del medio ambiente].

    p.mb-5 Para que las generaciones futuras puedan disfrutar en las mismas condiciones que se hace en la actualidad, sin desconocer el propsito empresarial de ser econmicamente rentable, #[strong se debe igualmente dar valor agregado a todos los procesos efectuados dentro de la organizacin], aumentando las posibilidades de preservacin de los ecosistemas y su aprovechamiento futuro en igualdad de oportunidades, produciendo alimentos ecolgicamente saludables para generaciones presentes y futuras, dentro de un entorno econmico, social y medioambiental; implementando buenas prcticas en todos sus procesos.

    .titulo-segundo
      #t_1_1.h4 1.1  Sistemas de produccin pecuaria

    .cajon--lavanda.p-4.mb-5.mb-lg-0.mt-5
      .row.justify-content-around.align-items-center
        .col-4.col-sm-6.col-md-3.mb-4.mb-md-0
          img.img-col(src="@/assets/curso/tema_1/img_2.png")

        .col-md.col-lg-8
          p Al diagnosticar los sistemas de produccin pecuaria en Colombia, se observa que en un alto porcentaje de esta prctica se sigue realizando de forma tradicional, con baja productividad y carente de tecnologas apropiadas para que sean rentables y sostenibles. 
            strong
              | Entendiendo como sostenibilidad empresarial a la capacidad de una organizacin en mantener su actividad comercial durante un tiempo prolongado, en constante interaccin con su entorno social, econmico, tcnico y medioambiental, garantizando su permanencia en el mercado y siendo econmicamente rentable.

    .row.justify-content-around.align-items-cente.mt-5.mb-5
      .col-md.col-lg-6
        p Por otra parte, se perciben problemas socioeconmicos, entre los que sobresalen: #[strong bajos niveles de escolaridad, carencia de servicios pblicos, escasez en servicios de salud, bajos recursos econmicos, vas de acceso en mal estado], etc. Tambin se observan #[strong factores tcnicos, genticos, comerciales y de infraestructura]; los cuales no permiten un desarrollo acorde con las exigencias de la creciente poblacin.

      .col-md.col-lg-6
        p La produccin pecuaria es considerada como #[strong la combinacin de actividades que se realizan dentro de cualquier explotacin de animales domsticos, cuya finalidad es producir carne, leche, huevos, pieles, lana, semoviente para cra, engorde, pie de cra, subproductos, etc]. Para luego ser aprovechados directamente por el hombre o terceros y as, obtener beneficios econmicos por su comercializacin. 

    p.mb-5 Los sistemas de produccin pecuaria, se pueden definir como #[strong el conjunto ordenado de normas y procedimientos que regulan el funcionamiento de la actividad pecuaria dentro de un determinado grupo poblacional]. En el que convergen elementos sociales, econmicos y culturales que facilitan preservar el bienestar de las comunidades y los ecosistemas a su alrededor.

    .row.justify-content-around.align-items-cente.mb-5
      .col-lg-8.mb-5.mb-lg-0
        .bloque-texto-b.p-4
          .bloque-texto-b__texto.h2.mb-0 Los sistemas de produccin pecuaria, son considerados como la estrategia social, econmica y cultural ms apropiada para mantener el bienestar de las comunidades, debido a que es la nica actividad que puede simultneamente proveer seguridad en el sustento diario, conservar ecosistemas, promover la conservacin de la vida silvestre y satisfacer los valores culturales y tradiciones

    p.mb-5 A travs del siguiente video del #[strong Instituto Colombiano Agropecuario (ICA)], se puede ver cmo la produccin pecuaria en Colombia aporta a la economa del pas: 

    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/2L91WMqw96A" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video: Leyenda del video

    .h4.mb-5 Clases o sistemas de produccin pecuaria

    p.mb-5 En Colombia se ha adoptado la clasificacin de sistemas de produccin pecuaria que se emplea en el continente latinoamericano para facilitar el anlisis de cada uno de los sistemas de produccin pecuaria, existente a lo largo y ancho del territorio colombiano. #[strong De tal forma que los tres tipos que se conocen son principalmente: los sistemas extensivos, los sistemas intensivos y los sistemas trashumantes], descritos a continuacin:

    .h4.mb-5 1. Sistemas extensivos

    .bloque-texto-a.p-4.p-md-5.mb-5
      .row.m-0.align-items-center.justify-content-between
        .col-lg-5.mb-4.mb-lg-0
          .h2.mb-0 Son el tipo de explotaciones pecuarias cuya actividad la realizan tradicionalmente en muchas zonas del territorio. 
          p Es la prctica que frecuentemente ejecutan los pequeos y medianos productores pecuarios del sector rural del pas.  
        .col-lg-7
          .bloque-texto-a__texto.p-4
            p En este tipo de explotacin existe una #[strong estrecha relacin entre los ecosistemas y el tipo de explotacin desarrollada, especialmente en los que la principal fuente de alimentacin son los pastos y forrajes]; tomados directamente por los semovientes del ecosistema natural, sin ninguna restriccin por parte de los animales para su alimentacin, en reas naturales o espacios adaptados para ello, denominados potreros.

    p.mb-5 La permanencia de estos animales en cada lugar est relacionada directamente con #[strong la capacidad de alimento disponible que supla los requerimientos nutricionales de la especie que lo habita]. Los productores delimitan las extensiones de terreno por medio de una cuerda elctrica adelante y atrs, con el fin de evitar deterioro de las pasturas.

    img.img-a.mb-5(src="@/assets/curso/tema_1/img_3.png")

    p.mb-5 Igualmente, se realiza la rotacin de potrero para permitir en cada periodo de florecimiento contar con nuevos pastos y no realizar la prctica de sobrepastoreo. Como ya se ha mencionado, #[strong la produccin extensiva hace parte de los sistemas ganaderos que son sostenibles por s solos por el hecho de que han permanecido vigentes en el tiempo]. De all, que se requieren de muy pocos recursos externos, reduciendo as el consumo de suplementos.

    p.mb-5 Este tipo de explotacin a pesar de que es duradera en el tiempo y puede verse como sostenible, #[strong no es eficiente productivamente debido a que se requiere de terrenos extensos para conseguir una buena productividad de pastos y forrajes que son la fuente de alimento principal del sistema productivo]. Por otra parte, se puede observar en ella que no afecta notoriamente a los ecosistemas, ya que an prevalece la biodiversidad nativa de flora y fauna. 

    .h4.mb-5 2. Sistemas intensivos

    .bloque-texto-a.p-4.p-md-5.mb-5
      .row.m-0.align-items-center.justify-content-between
        .col-lg-7
          .bloque-texto-a__texto.p-4
            p Para la consecucin de este propsito es necesario #[strong incorporar al sistema muchos recursos externos que conllevan a realizar inversiones econmicas altas para brindar las condiciones tcnicas, de infraestructura, equipos, tecnologa, alimentacin, sanidad, manejo y mano de obra requeridos de acuerdo al sistema de produccin implementado]. Con la implementacin de este tipo de explotaciones se incrementa la contaminacin del medio, afectando los recursos disponibles naturales y del medio ambiente, siendo estos sistemas insostenibles ecolgicamente.
        .col-lg-5.mb-4.mb-lg-0
          .h2.mb-0 En los sistemas de produccin intensivos, el principal objetivo es incrementar la productividad a menor costo 
          p Optimizando los recursos disponibles para ser competitivos y obtener mejores resultados en un menor tiempo siendo eficientes. 
          
    p.mb-5 Para incrementar la productividad, #[strong los semovientes deben estar en confinamiento durante todo su ciclo productivo y en un medio artificial adecuado con la infraestructura requerida para este fin]. Se requiere crear condiciones ptimas de confort destinadas como son la temperatura, la luz, la humedad y el suministro de alimento de alto nivel protenico y agua pura necesaria. Con esta tcnica de explotacin se incrementa el estrs en los semovientes, ya sea porque las condiciones de infraestructura no son las adecuadas o en la mayora de los casos se afectan los principios de bienestar animal. Aunque el suministro de alimento es el adecuado tcnicamente segn la especie y lo demandado por el mercado. 
        
    img.img-a.mb-5(src="@/assets/curso/tema_1/img_4.png")

    p.mb-5 En sntesis, los sistemas intensivos requieren de la apropiacin de tecnologas, para cumplir con el objetivo principal de obtener un alto beneficio econmico al menor tiempo posible, con el suministro de alimentos nutritivos, instalaciones adecuadas tcnicamente, control de enfermedades y mano de obra calificada o por medio de tecnificacin de procesos.

    p.mb-5 Esta prctica no es viable realizarla para los pequeos y medianos productores pecuarios o en especial, para los sectores rurales de los municipios colombianos, donde los productores carecen de recursos econmicos para hacer inversiones altas para volver productiva su explotacin.

    .h4.mb-5 3. Sistemas trashumantes

    .bloque-texto-a.p-4.p-md-5.mb-5
      .row.m-0.align-items-center.justify-content-between
        .col-lg-5.mb-4.mb-lg-0
          .h2.mb-0 En Colombia los sistemas trashumantes se realizan principalmente en actividades ganaderas, en zonas de difcil acceso en donde los periodos de lluvia son de tres meses.  
        .col-lg-7
          .bloque-texto-a__texto.p-4
            p All, #[strong los productores se ven obligados a llevar sus animales a potreros con pastizales verdes, que le garanticen alimento para su subsistencia del ganado durante la poca de escasez de alimento]. Esta actividad consiste en trasladar animales de zonas de escasez de pastos a otras de mejores condiciones medioambientales que aporten praderas adecuadas para su alimentacin.

    img.img-a.mb-5(src="@/assets/curso/tema_1/img_5.png")

    p.mb-5 Al finalizar el ciclo productivo regresan a la zona de origen donde se tiene asegurado el mercado de la produccin pecuaria. #[strong El traslado de los animales se realiza generalmente a pie, aprovechando las condiciones climticas que beneficien su traslado sin afectar mayormente su productividad]. 

    .titulo-segundo
      #t_1_1.h4 1.2 Buenas prcticas pecuarias

    p.mb-5 Las Buenas Prcticas Pecuarias (BPP), #[strong son una combinacin de normas que buscan garantizar la salud y el bienestar de los animales domsticos destinados a la produccin primaria y/o a la transformacin en productos y subproductos], dirigidos para la alimentacin humana. A travs de esta reglamentacin en Colombia, se design al Instituto Colombiano Agropecuario (ICA), como la entidad encargada de vigilar y controlar que los alimentos provenientes del sector primario estn libres de agentes biolgicos o fisicoqumicos que afecten la salud de los consumidores y lleguen a ellos en excelentes condiciones organolpticas de calidad e inocuidad. 

    p.mb-5 Los objetivos de las BPP, son principalmente:

    .list.mt-3
      .list__item
        .list__item__number
          h4.span.orange a
      .list__txt
        p.m-top Generar confianza entre los consumidores en referencia a la calidad e inocuidad del producto.
    hr.bd-hr
    .list.mt-3
      .list__item
        .list__item__number
          h4.span.orange b
      .list__txt
        p.m-top Disminuir el impacto ambiental.
    hr.bd-hr
    .list.mt-3
      .list__item
        .list__item__number
          h4.span.orange c
      .list__txt
        p.m-top Reducir la utilizacin de productos fitosanitarios.
    hr.bd-hr
    .list.mt-3
      .list__item
        .list__item__number
          h4.span.orange d
      .list__txt
        p.m-top Atenuar el uso del suelo y agua.
    hr.bd-hr
    .list.mt-3
      .list__item
        .list__item__number
          h4.span.orange e
      .list__txt
        p.m-top Propiciar el bienestar animal e incentivar la puesta en marcha de la responsabilidad frente a la salud y el entorno seguro de los trabajadores.

    p.mt-5.mb-5 En el siguiente grfico se observa #[strong la interaccin de las Buenas Prcticas Pecuarias que permiten alcanzar los objetivos que la fundamentan]. Hay que tener presente que para cada especie existe un protocolo particular sin que se desmarque de la norma general:
    
    .h4.mb-5.text-center.color_text Los principios de las Buenas Prcticas Agrcolas y Pecuarias

    img.img-a.mb-5(src="@/assets/curso/tema_1/img_6.svg")

    .cajon--amarillo.p-4.mb-5.lg-0
      p En cumplimiento de su misin, #[strong el ICA tiene la obligacin de participar activamente en la prevencin y el control de cualquier riesgo que afecte la salud de los colombianos] en lo referente a los procesos productivos en el sector primario, especialmente en fincas productoras de leche, porcinos, aves y semovientes destinados al sacrificio para consumo humano.

    p.mb-5 Por otra parte, #[strong es importante mejorar el bienestar laboral de los trabajadores del sector, tecnificar las explotaciones y contribuir con el bienestar animal]. Para la implementacin de las BPP, es necesario llevar registro ordenado de todas las actividades que se ejecutan en la unidad productiva, de esta manera #[strong el emprendedor tendr una visin objetiva del funcionamiento de su empresa pecuaria]. 

    p.mb-5 En el siguiente grfico, se pueden observar #[strong los decretos y resoluciones que describen la normatividad para el cumplimiento de las BPP en algunas especies]. 

    .row.justify-content-center.align-items-center.mb-5
