    .titulo-segundo
      #t_1_3.h4 1.3 Plan de produccin

    p.text_tamano.mb-5 #[strong.color_text El plan de produccin es un instrumento de diagnstico] 
      | #[strong que le permite a una empresa del sector pecuario generar informacin de tipo productivo.]

    .row.justify-content-around.align-items-cente.mt-5.mb-5
      .col-md.col-lg-6
        p Con base en la evaluacin tcnica realizada de los materiales requeridos y de la mano de obra necesaria teniendo en cuenta las caractersticas particulares de la especie que se pretende instaurar en la unidad productiva. Por otra parte, #[strong se requiere identificar las caractersticas exigidas por el nicho del mercado a donde se pretende comercializar]. De esta forma se disea, organiza y ejecuta la produccin que se procura cubrir con lo demandado por el mercado. 

      .col-md.col-lg-6
        p No es lo mismo disear un plan de produccin #[strong cuyo eje son las unidades agrcolas familiares que contribuyen al desarrollo de las comunidades rurales] y facilitan de una u otra forma conseguir recursos para mejorar su calidad de vida y realizar autogestin para su propio desarrollo, que disear un plan de produccin para #[strong una empresa cuyo objetivo es la sostenibilidad empresarial y obtener ganancias econmicas para reinvertir el fortalecimiento empresarial.] 

    .cajon--amarillo.p-4.mb-5.lg-0
      p Un plan de produccin es diseado temporalmente para un periodo que puede oscilar entre los 6-18 meses, en donde se definen todos los aspectos tcnicos, organizativos y consecutivos de recursos que se requieren para producir y volverlo eficiente, de acuerdo a lo diseado en el plan estratgico de la empresa.

    p.mb-5 Los componentes de un plan de produccin #[strong estn determinados por el tipo de explotacin pecuaria a instaurar y cada especie demanda una serie de recursos propios], entre los que se destacan:

    .row.mb-5
      .col-md-6.col-lg.mb-5
        .tarjeta-Avatar
          img(src='@/assets/curso/tema_1/img_12.png' alt='AvatarTop')
          .tarjeta.tarjeta--morada.p-4
            .h4.text-center Recursos humanos
            p.mb-3 #[strong Son los individuos que hacen parte del proceso productivo.]
            p Entre ellos estn: tcnicos pecuarios, productores y/u obreros, administradores, etc.


      .col-md-6.col-lg.mb-5
        .tarjeta-Avatar
          img(src='@/assets/curso/tema_1/img_13.png' alt='AvatarTop')
          .tarjeta.tarjeta--morada.p-4
            .h4.text-center Recursos tecnolgicos
            p #[strong Son los instrumentos que se utilizan para la puesta en marcha y desarrollo del ciclo productivo]: informacin de mercado, tecnologas productivas, tecnologas de manejo, sanidad, ambiente bioseguro, etc.

      .col-md-6.col-lg.mb-5
        .tarjeta-Avatar
          img(src='@/assets/curso/tema_1/img_14.png' alt='AvatarTop')
          .tarjeta.tarjeta--morada.p-4
            .h4.text-center Recursos financieros
            p #[strong Son todos los recursos monetarios necesarios] para la compra de insumos, materiales, semovientes, para el pago de servicios, mano de obra, acondicionamiento de infraestructuras, etc.; hasta que el producto est listo para su traslado al mercado para ser comercializado. 

    .titulo-segundo
      #t_1_4.h4 1.4 Parmetros tcnicos

    p.mb-5 Los parmetros tcnicos son datos fundamentales que se han estandarizado para cada una de las especies pecuarias, de acuerdo a su lugar de explotacin o ambiente ecolgico. 
      | 
      strong Estos parmetros corresponden a los fines productivos establecidos en la planeacin estratgica y se relacionan directamente con los aspectos reproductivos y de manejo propios de cada raza dentro de su ciclo productivo.

    .cajon--lavanda.p-4.mb-5.lg-0
      p De acuerdo a cada especie de animales, se distinguen varias clases de explotaciones pecuarias, donde cada una presenta una finalidad particular, siendo la de mayor impacto la de 
        |
        strong ganado bovino, porcino, ovino, caprino, avcola, pisccola y dems especies. 

    p.mb-5 En relacin a la finalidad de cada explotacin y de acuerdo a las especies, #[strong se pueden identificar algunos aspectos tcnicos que son la esencia para el cumplimiento de su misin y parmetros de resultados] que se pueden medir en das, kilogramos, porcentaje, cantidad, etc.; de acuerdo con el evento diseado de forma individual o en general para un determinado lote. Informacin que se debe registrar, analizar y comparar para tomar una decisin ms acertada. 

    p.mb-5 #[strong La principal finalidad de una explotacin es medir los aspectos reproductivos, de manejo y factores ambientales propios de cada raza]; optimizando los recursos se vuelven productivos y sostenibles en la explotacin. Los eventos que sobresalen son: 

