    .titulo-segundo
      #t_1_5.h4 1.5 Programacin de actividades pecuarias

    .list.mt-3.mb-5
      .list__item
        .list__item__number
          h4.span.amarillo 1
      .list__txt
        p La programacin de actividades #[strong es el proceso consecutivo a la planificacin estratgica y define los pasos operacionales, estableciendo dnde y cundo se va a plasmar el conjunto de especies a implementar]. La programacin es la herramienta ejecutora en que se ve materializada en la planificacin de las actividades pecuarias involucradas. Dentro de las actividades pecuarias se incluye #[strong el conjunto de animales domsticos con fines econmicos explotados tradicionalmente como son: bovinos, porcinos, ovinos, caprinos, peces, aves, etc].

    p.mb-5 Existen diferentes mtodos que se emplean para programar actividades y definir el tiempo proyectado para el desarrollo de cada actividad. En el sector pecuario #[strong la seleccin de una adecuada metodologa est relacionada con las caractersticas y necesidades propias de la especie y exigencias del mercado]. En el sector primario se utilizan tres de los mtodos ms comunes y eficaces en el cumplimiento de las metas propuestas, los cuales son: 

    TabsC.mb-5
      .py-3.py-md-4(titulo="Diagrama de Gantt")
        img.img-a.mb-5(src="@/assets/curso/tema_1/img_15.png")
        .row.justify-content-center
          .col-md-10.mb-4.mb-md-0
            p Esta herramienta por su sencillez es til para programar actividades en cualquier tipo de explotacin y se compone de dos ejes: uno vertical y otro horizontal, donde se seala por un lado las actividades y por el otro el tiempo del proyecto. 
              | 
              strong El Diagrama de Gantt refleja, a travs de diagramas de barra horizontales, la distribucin y duracin en tiempo de cada una de las labores diarias a ejecutar.
        
      .py-3.py-md-4(titulo="Diagrama de Pert")
        img.img-a.mb-5(src="@/assets/curso/tema_1/img_16.png")
        .row.justify-content-center
          .col-md-10.mb-4.mb-md-0
            p Esta tcnica de planificacin, es utilizada de acuerdo a la complejidad de la explotacin pecuaria. Es una representacin grfica de las tareas planificadas en relacin al tiempo proyectado en cada una de las fases del ciclo a desarrollar.
        
      .py-3.py-md-4(titulo="Cronograma de actividades")
        .row.justify-content-center
          .col-md-10.mb-4.mb-md-0
            p.mb-5 Es el instrumento ms utilizado en la planificacin de activadas de la unidad pecuaria. Permite establecer el tiempo que se proyecta realizar la actividad. Con la utilizacin de esta herramienta se puede realizar una adecuada distribucin de las labores diarias, estableciendo el tiempo de duracin y los tiempos establecidos para cada uno. Adems, permite a la organizacin realizar seguimiento y control de cada una de las actividades que se desarrollan en cada unidad productiva.
            p En la actualidad, con el avance e incorporacin de las TIC en todos los procesos productivos, la elaboracin de un cronograma es mucho ms fcil. Igualmente, en el mercado se comercializa software especializado que facilita la planeacin de actividades.

      .py-3.py-md-4(titulo="Labores pecuarias")
        .row.justify-content-center
          .col-md-10.mb-4.mb-md-0
            p.mb-5 Teniendo presente lo definido anteriormente en relacin a la produccin pecuaria, que est considerada como la combinacin de actividades que se realizan dentro de cualquier explotacin de animales domsticos. En el mismo sentido, debe existir personal idneo responsable que se encargue de realizar esta labor pecuaria. Actividad que corresponde al conjunto de trabajos organizados y diseados en la planeacin, de acuerdo con el tipo de explotacin y requerimientos tcnicos definidos para el cumplimiento de las metas.
            p Como conclusin, se puede establecer que las labores pecuarias son el conjunto de tareas o funciones organizadas que son realizadas por una o varias personas con un perfil profesional idneo o profesin que contribuye en la consecucin de las metas y objetivos de la empresa.
        
