    p.text_tamano.mb-5 #[strong La comunicacin empresarial es] #[strong.color_text un proceso que se fundamenta en la emisin, transmisin e intercambio de mensajes ] 
      | #[strong que se efectan entre un emisor y un receptor o viceversa, mediante un canal de comunicacin que se designe para ello.]

    p.mb-5 En el siguiente grfico, se identifican los dos tipos de canales de comunicacin que existen: 

    .row.justify-content-center.mb-5
      .col-md-4.col-lg-5
        .tarjeta.tarjeta--amarilla.p-4
