    .row.justify-content-around.align-items-cente.mt-5.mb-5
      .col-md.col-lg-6
        p La comunicacin empresarial se ha convertido en #[strong una estrategia fundamental que permite establecer la visin corporativa y posicionar su marca en el mercado]. Para hacer esto posible la comunicacin debe ser asertiva, tanto en su interior, generando excelentes relaciones entre los trabajadores de la organizacin y a nivel externo, donde se efecta entre otras empresas del sector, entre la empresa y los clientes o entre la organizacin y los proveedores.

      .col-md.col-lg-6
        p La comunicacin que se realiza #[strong entre la empresa y los clientes tiene el propsito de comunicar los productos que se tienen a la venta, Indicando las caractersticas de cada uno de ellos]. Esta comunicacin debe ser permanente, ya que los objetivos planteados por el departamento de comunicacin de la empresa, traza el camino a seguir para alcanzar la visin de la organizacin apoyada por todas sus dependencias.  

    .cajon--azul.p-4.mb-5.lg-0
      p En resumen, #[strong la comunicacin interna o propia de la empresa, es la que fluye en ambas vas entre los diferentes trabajadores y dependencias de la empresa pecuaria] y se efecta en el interior de la organizacin.

    p.mb-5 Con la incorporacin de las tecnologas de la informacin y la comunicacin en una empresa, #[strong es muy comn realizar intercambio de informacin por medio de canales en los que se proyecta realizar estrategias comerciales o de imagen corporativa].

    .row.justify-content-center.mb-5
      .col-md-6.col-lg-4.mb-5
        .tarjeta-Avatar
