            p Permite crear #[strong acciones y estrategias a travs de medios digitales como pginas web, blogs corporativos, redes sociales] como Facebook, Instagram y Twitter, email, marketing, publicidad digital, etc.

      .col-md-6.col-lg-4.mb-5
        .tarjeta-Avatar
