          .tarjeta.tarjeta--amarilla.p-4
            .h4.text-center Comunicacin offline
            p Es la herramienta que se lleva a cabo con la #[strong utilizacin de los medios tradicionales de comunicacin como son tarjetas de presentacin, afiches, telfono, pendones], etc.

    p.mb-5 Toda empresa del sector pecuario debe #[strong disear sus objetivos de comunicacin con un enfoque estratgico y as difundir sus actividades comerciales e imagen corporativa], sin desconocer elementos claves para su sostenibilidad como convencer, persuadir, influir e informar. Es fundamental evitar comunicar algo negativo, poco profesional o sin fundamento cientfico que, en vez de fortalecer a la empresa, la puede estar perjudicando.

    .titulo-segundo
      #t_1_7.h4 1.7 Manuales de procedimiento

    .row.justify-content-around.align-items-cente.mt-5.mb-5
      .col-md.col-lg-6
        p.text_tamano
          |
          strong  El manual de procedimiento es un documento elaborado de acuerdo con los requerimientos tcnicos de la unidad productiva. 

      .col-md.col-lg-6
        p En l se describe de forma secuencial, detallada e integral, #[strong el paso a paso de cada una de las actividades que se realizan diariamente para cumplir los objetivos y metas de la empresa]. 

    p.mb-5 Igualmente, este documento #[strong facilita la incorporacin de nuevos trabajadores que realicen una actividad especfica]. Este manual es propio de la empresa y tiene carcter reservado, debido a que all se encuentra informacin relevante de las caractersticas de cada producto, del sistema productivo, de las tcnicas alimenticias y de los procedimientos funcionales de la empresa. 

    p.mb-5 El objetivo de los manuales de procedimientos se fundamenta en la utilidad que presta a la organizacin empresarial. Sirve como instrumento de comunicacin e informa a sus trabajadores las funciones asignadas a cada unidad productiva, los procedimientos que se llevan a cabo, define responsabilidades, impide realizar repeticiones, facilita la cooperacin, ahorro de tiempo, optimiza el aprovechamiento de recursos humanos, materiales, financieros y tecnolgicos disponibles en el cumplimiento de la misin empresarial.

    p.mb-5 El siguiente grfico nos resume los beneficios de tener un manual de procedimientos en una empresa especialmente del sector pecuario. 

    img.mb-5.img-a(src="@/assets/curso/tema_1/img_21.png")

    p.text_tamano.mb-5 #[strong El manual de procedimientos es] #[strong.color_text una herramienta que permite desarrollar de manera eficiente las diferentes actividades que realiza una empresa] 
      | #[strong y ofrece informacin de los procedimientos que se llevan a cabo en la unidad productiva.]

    p.mb-5 En l, se incluyen los objetivos del procedimiento, el rea de aplicacin, las polticas, documentos esenciales, los sistemas informticos, los formularios, las herramientas utilizadas, las definiciones importantes, la descripcin de cada uno de los pasos que se realizan en cada actividad y se indica el responsable del proceso. 

    .cajon--amarillo.p-4.mb-5
      .row
        .col-md-4.col-lg-2
          img.img-a(src="@/assets/curso/tema_1/img_22.png")
        .col-md-4.col-lg-8
          p Un manual de procedimientos se elabora particularmente para cada una de las empresas, 
            | 
            strong se estructura en el plan de manejo eficiente de la produccin de acuerdo al ciclo productivo y requerimientos tcnicos y exigencia del mercado. All se incorporan los protocolos para el entrenamiento de los nuevos trabajadores, las normas de produccin, la estandarizacin de actividades, el suministro de alimento y de agua, la infraestructura, el manejo de semoviente, ternero, sanidad, bioseguridad, insumos, etc. 

    p.mb-5 #[strong Los manuales de procedimiento son fundamentales para el desempeo general de la empresa] y es necesario que todos los trabajadores de la organizacin conozcan y cumplan con lo que reza en cada manual existente. En una empresa se pueden identificar los siguientes tipos de manuales:

    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--morada_1")
      .row.justify-content-right(titulo="Manual de procedimientos")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p.mb-3 En este tipo de manual se describe el paso a paso que se debe seguir para que la empresa funcione articuladamente en la consecucin de los objetivos propuestos, explicando cules son las tareas que se debe realizar en cada unidad y cmo se vincula con las dems. 
            | 
            strong Se deben describir las actividades, procesos, objetivos y dems, para que los funcionarios sean diligentes en el cumplimiento de sus compromisos laborales y en relacin con su entorno. 

          p Tambin se especifican los insumos que debern utilizar para el cumplimiento de su labor, tiempo y fechas lmite para la concrecin de la tarea, especificando la funcin de cada uno de los miembros de la unidad.

      .row.justify-content-right(titulo="Manuales histricos")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p Los manuales conocidos con este nombre, son los que cumplen la misin de recopilar la historia de la empresa. La finalidad de este tipo de manual es perpetuar la cultura corporativa, buscando transmitir y preservar los elementos que han permitido a la empresa prevalecer en el mercado. En l se incluye el ao de fundacin, quin la constituy, cmo fue creciendo y evolucionando, cules han sido los mayores logros alcanzados y dems informacin relevante que permita su imagen corporativa.

      .row.justify-content-right(titulo="Manuales mltiples")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p El manual mltiple contiene informacin diversa de la organizacin. La idea es resumir en este documento bien redactado, dividido por sesiones y de fcil acceso, transmitiendo lineamientos instruccionales provenientes de los directivos a todos los miembros de la empresa. Por otra parte, se trata de una herramienta sencilla de acuerdo a su contenido especfico y detallado en la bsqueda de soluciones rpidas y prcticas.

      .row.justify-content-right(titulo="Manuales tcnicos")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p En el caso puntual de una empresa del sector son de suma importancia este tipo de manuales, ya que se centran en explicar con detalle y ordenadamente las diferentes tcnicas que se deben utilizar dentro de cada ciclo productivo. Son una herramienta tcnica que permite el desempeo de su labor de manera correcta y eficiente, con los avances de la tecnologa y los nuevos descubrimientos cientficos. Estos manuales deben ser actualizados permanentemente para conseguir en corto plazo alcanzar el logro de los objetivos propuestos.

      .row.justify-content-right(titulo="Manuales de calidad")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p Como su nombre lo indica, son los que determinan cmo debe ser la calidad de los productos, en relacin con las distintas reas de la entidad. Esto contempla desde la calidad del producto, pasando por procesos, hasta alcanzar objetivos propuestos que deben alcanzarse. En ellos se plasma cmo debe ser la medida y la calidad estndar que debe mantenerse durante todo el ciclo productivo, sin afectar el producto como tal o tener que hacer correcciones y modificaciones que afecten el desempeo de la empresa.

      .row.justify-content-right(titulo="Manuales normativos")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p Son los que plantean de forma detallada, cmo se debe proceder ante cierta situacin, as como tambin cul es el plan de mejora de la dificultad presentada. 

      .row.justify-content-right(titulo="Manuales de cargos")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p Este tipo de manual es de suma importancia para una empresa, ya que se fundamenta en el rol de cada puesto que el trabajador debe desempear y su responsabilidad, como tambin cul es la relacin del mismo con los otros cargos de la empresa. Esto contribuye a evitar sobrecargar de tareas a un trabajador, acrecentar su responsabilidad y mejorar las relaciones interpersonales dentro de la organizacin. 

      .row.justify-content-right(titulo="Manuales de ventas")
        .col-md-2.mb-4.mb-md-0
        .col-md-8.mb-4.mb-md-0
          p Documento en el que se establecen las tcnicas de comercializacin, pautas y procedimientos para llevar a cabo las relaciones comerciales entre la empresa y los clientes, correspondientes a la venta de productos obtenidos en la entidad. En ellos se describe cmo es el comportamiento de la competencia, en qu reas se encuentran los distintos clientes, proveedores y productos, as como cul ser la estrategia diseada para llevar a cabo la venta de los productos.

    .titulo-segundo
      #t_1_7.h4 1.8 Legislacin laboral

    p.mb-5 La legislacin laboral es #[strong una rama del derecho que puede entenderse como el conjunto de leyes y normas que tiene por finalidad regularizar las actividades laborales de todos los trabajadores, como tambin las obligaciones] de acuerdo al perfil laboral a desempear; igualmente rige para el empleador de acuerdo a la normatividad colombiana. Las leyes laborales facilitan que empresarios y trabajadores establezcan un punto de referencia en comn acuerdo para realizar un oficio. Es decir, que la normatividad funciona como el reglamento bsico por el cual se regula toda actividad productiva, en virtud de mejores condiciones de trabajo, estabilidad y seguridad laboral.


    .cajon--amarillo.p-4.mb-5
      p La legislacin laboral en Colombia, mediante leyes, normas, resoluciones y acuerdos, #[strong es la encargada de vigilar y controlar todo tipo de contrato laboral que se realice entre los derechos del trabajador, los derechos laborales y el empresario], sea este privado o pblico. 

    p.mb-5 De acuerdo a la ley laboral en Colombia, #[strong se contemplan cuatro tipos de contratos en relacin al tiempo de duracin del contrato]: 

    .row.mb-5
      .col-sm-6.col-lg-3.mb-6.mb-lg-0
        .tarjeta.tarjeta--boton.p-4.mb-5.min-h
          .row.mb-3
            .col-3
              img.img-a(src='@/assets/curso/tema_1/img_23.png')

          .h5 A trmino indefinido
          p Como su nombre lo indica, 
            | 
            strong no tiene estipulado ningn tiempo de finalizacin o plazo.
  
      .col-sm-6.col-lg-3.mb-4.mb-lg-0
        .tarjeta.tarjeta--boton.p-4.min-h
          .row.mb-3
            .col-3
              img.img-a(src='@/assets/curso/tema_1/img_23.png')

          .h5 A trmino fijo
          p Se tiene estipulado 
            | 
            strong un tiempo que no puede ser superior a tres aos.
  
      .col-sm-6.col-lg-3.mb-4.mb-sm-0
        .tarjeta.tarjeta--boton.p-4.min-h
          .row.mb-3
            .col-3
              img.img-a(src='@/assets/curso/tema_1/img_23.png')

          .h5 Obra o labor
          p Corresponde al #[strong tiempo que se requiere para la ejecucin de una actividad] determinada por el empresario.
  
      .col-sm-6.col-lg-3
        .tarjeta.tarjeta--boton.p-4.min-h
          .row.mb-3
            .col-3
              img.img-a(src='@/assets/curso/tema_1/img_23.png')

          .h5 Temporal
          p Est determinado para una #[strong duracin inferior a un mes y su finalidad es la realizacin de actividades complementarias] a las desarrolladas por la empresa en algn momento determinado.

    .row.justify-content-around.align-items-cente.mb-5
      .col-md.col-lg-6
        p #[strong Toda actividad laboral se realiza para en contraprestacin recibir un salario mensual, previamente acordado entre el trabajador y el empresario]. En Colombia est reglamentado que anualmente se fija el valor del #[strong Salario Mnimo Legal Mensual Vigente (S.M.L.M.V.)], por parte del Ministerio del Trabajo y se establece el sueldo mnimo que puede recibir una persona por el trabajo desarrollado. Salario que por ninguna razn o motivo puede ser inferior a un (S.M.L.M.V.).

      .col-md.col-lg-6
        p Por otra parte, el trabajador y el empresario tienen la libertad de pactar beneficios complementarios que no son constitutivos de salario; como por ejemplo alimentacin, vestuario o primas extralegales; #[strong para que estos beneficios se consideren como pagos no constitutivos de salario, no pueden ser de afectacin directa de las actividades que realiza el empleado]. Es decir, las comisiones o bonos por cumplimiento de metas individuales son consideradas obligatoriamente como pagos salariales.

    img.mb-5.img-a(src='@/assets/curso/tema_1/img_24.png')

    .cajon--amarillo.p-4.mb-5
      p La legislacin tambin permite acordar #[strong jornadas laborales flexibles que se adapten a las necesidades de los trabajadores]. Esta ley excluye a los empleados que desempeen cargos de direccin, confianza y manejo.

