            p La finalidad del departamento de recursos humanos es #[strong mantener las excelentes relaciones entre los trabajadores y empresarios, generando beneficios y ventajas recprocas que permiten a los trabajadores aprovechar sus capacidades y habilidades]; aumenta de esta manera la productividad empresarial y fomenta las buenas relaciones interpersonales entre los trabajadores, crea motivacin y buen clima laboral.

