    .titulo-segundo.mt-5
      #t_1_8_2.h5 1.8.2 Normativa

    p.mt-5.mb-5 #[strong La normativa es un conjunto de leyes, decretos o reglamentos que regula toda actividad laboral para el cumplimiento de la misin empresarial, sea privada o pblica]. Estando enmarcada dentro de la normatividad legal vigente, en las actuales condiciones de la economa mundial se observa que esto no se cumple, puesto que, al existir un deterioro en los mecanismos de contratacin laboral, #[strong se ha incrementado la informalidad laboral especialmente en las zonas rurales de los municipios]. 

    .row.justify-content-around.align-items-cente.mb-5
      .col-lg-8.mb-5.mb-lg-0
        .bloque-texto-b.p-4
          .bloque-texto-b__texto.h2.mb-0 Como lo referencia la Sociedad de Agricultores de Colombia (SAC), que describe en sus estudios que la informalidad laboral en el sector agricultor lleg al 86%, y el promedio en el pas est alrededor del 44%. Segn la entidad, se hace necesario cambiar las normas laborales para actividades agrcolas y pecuarias a nivel nacional. De acuerdo a lo planteado por el gremio, se encuentra que el trabajador rural puede tener ms de un empleador en el da, realizando diferentes labores, asimismo se evidencia la falta de un rgimen de contratacin que rena aspectos como el jornal diario y el trabajo por horas, mientras garantiza el pago de prestaciones de salud y pensin del trabajador, lo cual permitira reducir la informalidad en el sector

    p.text_tamano.mb-5 #[strong Es de conocimiento que] #[strong.color_text Colombia es uno de los pases ms ricos en recursos naturales] 
      | #[strong y por este motivo el sector agrcola y pecuario ha tenido un protagonismo importante para la economa nacional.]

    p.mb-5 Tambin por el aumento de la poblacin se requiere de mayor oferta de alimentos de excelente calidad y de mayor productividad. 
      |
      strong Para cumplir con los requerimientos del mercado es necesario generar ms empleo formal para los trabajadores rurales del pas.

    .row.m-0.align-items-center.justify-content-between.mb-5
      .col-lg-5.mb-4.mb-lg-0
        img.img-a(src='@/assets/curso/tema_1/img_25.png')
      .col-lg-7
        .cajon--azul.p-5.bloque-txt En el mismo sentido a lo manifestado anteriormente y lo descrito en el boletn de mercado laboral de mayo a julio de 2019, realizado por el #[strong Departamento Administrativo Nacional de Estadstica (DANE)], se revel que #[strong la rama del sector de la agricultura, ganadera, caza, silvicultura y pesca ocup el 60,6% de la poblacin] en este periodo.

    

    .row.justify-content-around.align-items-cente.mb-5
      .col-md.col-lg-6
        p Ante la mejora continua de la generacin de empleo en este sector, la fuerza laboral tambin encuentra una oportunidad de crecimiento para desarrollar sus habilidades y conocimientos especficos que ayudarn a responder a las nuevas demandas del mercado laboral, la cual exige 
          |
          strong competencias tcnicas cada vez ms especializadas para potencializar el mercado nacional y atraer la inversin extranjera.

      .col-md.col-lg-6
        p Desde cada experiencia, se debe reconocer #[strong lo importante que es la adquisicin de nuevos conocimientos y habilidades en los trabajadores colombianos y por ello, el sector pecuario requiere de un mayor talento humano, con formacin tcnica y desarrollo de habilidades profesionales] que le permitan ser eficientes en el mercado laboral existente, contribuyendo para el fortalecimiento empresarial, su productividad y sostenibilidad.
    
    .titulo-segundo
      #t_1_8_3.h5 1.8.3 Bienestar animal

    p.mb-5 #[strong El concepto de bienestar animal est directamente relacionado con el estado fsico o corporal de un semoviente en el entorno en que se encuentra] o en un ciclo productivo y la forma como el animal afronta las condiciones medioambientales; en otras palabras se puede decir que si el semoviente se encuentra #[strong sano, cmodo, bien alimentado, en seguridad, en confort y expresa de forma innata su comportamiento y no hay presencia de malos olores, estrs, decaimiento o desasosiego]; el animal est en un ambiente saludable y de bienestar.

    .cajon--azul.p-4.mb-5
      .row
        .col-md-4.col-lg-2
          img.img-a(src="@/assets/curso/tema_1/img_26.png")
        .col-md-4.col-lg-8
          p En lo que se refiere a los aspectos de sanidad, #[strong el principio de la ciencia es prevenir enfermedades]. De all, el uso de productos veterinarios, ya que existen tratamientos que requieren de un tiempo de retiro, para poder ser consumidos, si el sacrificio es con destino al consumo humano. 

    .row.justify-content-around.align-items-cente.mb-5
      .col-md.col-lg-6
        p #[strong El ideal de toda explotacin pecuaria es suplir las necesidades bsicas de los animales y preservar su entorno saludable], en funcin de reducir los costos en salud y por otra parte que la empresa sea productivamente rentable y sostenible. #[strong El bienestar de los animales se sintetiza en cinco aspectos que se consideran como prioridad del animal] y el empresario los debe respetar y cumplir a cabalidad. 

      .col-md.col-lg-6
        p En el siguiente grfico titulado #[strong Las 5 libertades de los animales], se enumeran los cinco aspectos que no le deben faltar a una explotacin pecuaria. Cada uno de ellos con sus propios requerimientos tcnicos, que deben ser suministrados durante su ciclo productivo y as garantizar una adecuada vida.

    img.mb-5.img-a(src="@/assets/curso/tema_1/img_27.png")

    p.mb-5 Un factor indispensable dentro de una explotacin pecuaria es #[strong tener responsabilidad tica durante toda la cadena productiva, indistintamente de la especie animal], tipo de crianza y uso, pues los semovientes deben cumplir su ciclo productivo en las mejores condiciones de bienestar animal evitando que entren en situaciones de estrs, dolor, temor, angustia y desasosiego. 

    p.mb-5 Todos los aspectos descritos anteriormente hacen referencia a un solo individuo animal. Por otra parte, #[strong se debe cumplir con los cuatro principios grupales que requiere cada especie pecuaria], para lograr su propsito productivo y permitir alcanzar los objetivos de la empresa; los cuales son:


    TabsC.mb-5
      .py-3.py-md-4(titulo="Buena alimentacin")        
        .row.justify-content-center
          .col-md-2.mb-4.mb-md-0
            img.img-a.mb-5(src="@/assets/curso/tema_1/img_28.png")
          .col-md-8.mb-4.mb-md-0
            p Su suministro est determinado tcnicamente por su #[strong peso corporal, la provisin de agua y alimento en cantidad suficiente y calidad nutricional apropiada] capaz de satisfacer sus requerimientos nutricionales. Se encuentran los procesos biolgicos y fisiolgicos, correspondientes a su edad, etapa productiva y condicin fisiolgica de salud.
        
      .py-3.py-md-4(titulo="Ambiente adecuado")
        .row.justify-content-center
          .col-md-2.mb-4.mb-md-0
            img.img-a.mb-5(src="@/assets/curso/tema_1/img_29.png")
          .col-md-8.mb-4.mb-md-0
            p Son las condiciones de confort fsico y social sin exposicin extrema a diversos factores de estrs como el estmulo trmico, el reducido espacio fsico requerido tcnicamente por animal de acuerdo a la especie y tipo de produccin.
        
      .py-3.py-md-4(titulo="Buena salud")
        .row.justify-content-center
          .col-md-2.mb-4.mb-md-0
            img.img-a.mb-5(src="@/assets/curso/tema_1/img_30.png")
          .col-md-8.mb-4.mb-md-0
            p Se resalta el manejo de sanidad preventiva y de ser necesario, el empleo de tratamientos veterinarios acordes a la patologa presentada en aras de eliminar el dolor, las lesiones y/o enfermedades.

      .py-3.py-md-4(titulo="Entorno social")
        .row.justify-content-center
          .col-md-2.mb-4.mb-md-0
            img.img-a.mb-5(src="@/assets/curso/tema_1/img_31.png")
          .col-md-8.mb-4.mb-md-0
            p Es fundamental favorecer el comportamiento innato de la especie, sin fomentar alteraciones en su comportamiento por falta de bienestar en alguno de los tres aspectos anteriores.

    p.mb-5 Como sntesis del bienestar animal, se precisan los siguientes #[strong 12 criterios que definen el buen comportamiento animal]. Siempre y cuando se cumplan a cabalidad durante todo su ciclo productivo:

    .row.mb-5
      .col-md.mb-5.mb-sm-0 
        ol.lista-ol.lista-ol--separador
          li 
            span.text-bold 1. 
            | Los animales deben tener una alimentacin suficiente y adecuada para no sufrir hambre.
          li 
            span.text-bold 2. 
            | Los animales deben tener acceso a un suministro de agua pura y bacteriolgicamente apta para consumo animal en cantidad suficiente y adecuada para no sufrir sed.
          li 
            span.text-bold 3. 
            | Los animales deben estar cmodos.
          li 
            span.text-bold 4. 
            | Los animales deben tener una temperatura adecuada para estar en confort climtico.
          li 
            span.text-bold 5. 
            | Los animales deben tener suficiente espacio para moverse con libertad.
          li 
            span.text-bold 6. 
            | Los animales no deben presentar lesiones fsicas.
          li 
            span.text-bold 7. 
            | Los empresarios deben mantener las condiciones ambientales y de higiene para minimizar la presencia de enfermedades.
          li 
            span.text-bold 8. 
            | Los animales no deben sufrir dolor durante el manejo zootcnico.
          li 
            span.text-bold 9. 
            | Los animales deben poder manifestar su comportamiento innato, propio de su especie.
          li 
            span.text-bold 10. 
            | Los animales deben ser manejados correctamente en todas sus rutinas diarias.
          li 
            span.text-bold 11. 
            | Se debe promover el comportamiento natural de la especie donde se sientan seguros y activos emocionalmente.
          li 
            span.text-bold 12. 
            | Se debe evitar el estrs.

    .row.justify-content-center.mb-3
      .col-md-2.mb-4.mb-md-0
        img.img-a.mb-5(src="@/assets/curso/tema_1/img_32.png")
      .col-md-8.mb-4.mb-md-0
        p #[strong Los animales domsticos destinados a la produccin de alimentos tienen alto porcentaje de sufrir estrs], as sea de uno de los criterios enumerados anteriormente, lo que conlleva a #[strong generar menores niveles de produccin y baja calidad de los productos]. Por eso, las malas prcticas de manejo, instalaciones defectuosas o lesiones fsicas entre otras causas, generan estrs en los semovientes, generando agresividad, problemas de manejo, cada de la produccin o susceptibilidad a adquirir enfermedades. 

    p.mb-5 Como poltica de productividad en la empresa pecuaria, #[strong se busca minimizar la generacin de estrs en los animales realizando actividades de manejo dentro de un ambiente de bienestar y de produccin rentables y sostenibles], especialmente en producciones intensivas de todas las especies. 

    .cajon--azul.p-4.mb-5.lg-0
      p #[strong El bienestar animal es fundamental en todas las cadenas de produccin pecuarias y en todo su ciclo sin importar el eslabn], ya sea desde la tenencia, reproduccin, produccin, comercializacin, transporte o quienes realicen propiamente las actividades productivas de la explotacin. 

    .titulo-segundo
      #t_1_9.h4 1.9 Comportamiento animal

    p.text_tamano.mb-5 #[strong El comportamiento animal se conoce como etologa y hace parte de las ciencias biolgicas que se encargan del ] #[strong.color_text estudio de la conducta propia e innata de los animales en su entorno natural.]

    p.mb-5 Es fundamental identificar que el comportamiento animal #[strong comprende todas las formas de interaccin en que los semovientes se relacionan con otros organismos y el medio ecolgico]. El comportamiento animal tambin se puede entender como la respuesta a un cambio en su rutina diaria, debido a un estmulo interno o externo.

    p.mb-5 Algunos investigadores manifiestan que el comportamiento animal se puede agrupar en dos tipos de comportamiento:

    .row.justify-content-center.mb-5
      .col-md-6.col-lg-4.mb-5
        .tarjeta.tarjeta--amarilla.p-4
          .h4 Comportamiento innato
          p.mb-4 Como su nombre lo indica, es un comportamiento que 
            |
            strong por naturaleza est dentro de su ambiente natural.

      .col-md-6.col-lg-4.mb-5
        .tarjeta.tarjeta--amarilla.p-4
          .h4.text-center Comportamiento aprendido
          p Se relaciona con la capacidad para #[strong realizar cambios en su actuar debido a la experiencia, manejo y adaptabilidad]; donde se denomina el aprendizaje.

    p.mb-5 Con el estudio etolgico de las especies, es posible #[strong determinar el xito de una explotacin pecuaria, ya que, conociendo su comportamiento nativo, su origen gentico, fenotipo y ambiente, es posible acondicionar el ambiente fsico, aprovechando los factores hereditarios y ambientales para que el temperamento del animal, sea lo ms aproximado a su entorno natural]. No hay que desconocer que los semovientes, nacen con el instinto de reproduccin, de defensa, de alimentacin, de proteccin, entre otros. En algunos casos y de acuerdo a su naturaleza y domesticacin unos son ms agresivos que otros animales domsticos. 

    p.mb-5 Resumiendo un poco lo anterior, a continuacin, se puede observar un clip donde se refleja #[strong el comportamiento animal en una especie domstica]:

    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/2L91WMqw96A" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    .titulo-segundo
      #t_1_10.h4 1.10 Especie

    p.mb-5 Se entiende por especie animal al #[strong conjunto de organismos que nacen, crecen, se reproducen y transmiten la misma informacin gentica de generacin] en generacin. En general, los individuos de una especie se reconocen porque son similares fenotpicamente y realizan la misma funcin productiva. Sin embargo, existe la posibilidad que dentro de una misma especie se observen diferencias fsicas entre estos.

    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/2L91WMqw96A" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    p.mb-5 Los animales se clasifican de acuerdo a su estructura de sostenimiento; es decir, #[strong si cuentan con una estructura articulada de huesos o columna vertebral o no]. A los animales que tienen por soporte estructural una columna, se les denomina animales vertebrados y los que no la poseen se llaman animales invertebrados. #[strong Todos los semovientes estn conformados por huesos que conforman la espina dorsal, organizada perfectamente en tres partes: cabeza, tronco y extremidades]. La columna vertebral es la estructura principal de sostenimiento del esqueleto, adems protege la mdula espinal y permite al semoviente desplazarse en posicin de pie.

    img.img-a.mb-5(src="@/assets/curso/tema_1/img_33.png")

    .titulo-segundo
      #t_1_10_1.h5 1.10.1 Etapas de desarrollo

    p.text_tamano.mb-5 #[strong Son funciones vitales de todos los seres vivos y en especial de los semovientes: ] #[strong.color_text nacer, crecer, reproducirse, relacionarse y morir.]

    .cajon--amarillo.p-4.mb-5.lg-0
      p Cada especie basada en su finalidad productiva se debe enfatizar en una de estas etapas, las cuales permiten a la unidad productiva cumplir con los fines y objetivos propuestos. Todos los seres vivos de acuerdo a su especie, cumplen su ciclo de vida dentro de un ambiente ecolgico propicio.

    p.mb-5 En las actividades pecuarias, aunque cada especie tiene su propio ciclo de vida determinado, todas pasan por las mismas etapas como se ha mencionado anteriormente. #[strong La primera etapa de la vida de los animales inicia con el nacimiento, despus sigue el desarrollo o crecimiento, luego es potestativo e inherente a su capacidad reproductiva la de perpetuar la especie y por ltimo el morir]. Cada una de las etapas de desarrollo animal, se convierte en una oportunidad de negocio dentro de la cadena productiva del sector primario.

    p.mb-5 De acuerdo a lo anterior, en el siguiente grfico se puede apreciar un     
      |
      strong ejemplo de ciclo de vida: 

    img.img-a.mb-5(src="@/assets/curso/tema_1/img_34.png")

    
    AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta tarjeta--morada_2")
      .row(titulo="1. Nacimiento")
        .col-md-4
          img.img-a.mb-5(src="@/assets/curso/tema_1/img_35.png")
        .col-md-7.mb-4.mb-md-0
          p #[strong Es la primera etapa del ciclo de vida de un semoviente]; el cual se logra gracias a un desarrollo fetal normal desde su concepcin hasta el nacimiento de un neonato. Para lograr esto lo primero en hacer es #[strong cubrir los requerimientos nutricionales de la madre gestante y aportar alimentos balanceados para que el feto alcance su desarrollo durante la etapa de gestacin que le corresponde segn sea la especie animal]. 
      

      .row(titulo="2. Desarrollo")
        .col-md-4
          img.img-a.mb-5(src="@/assets/curso/tema_1/img_36.png")
        .col-md-7.mb-4.mb-md-0
          p A continuacin del nacimiento sigue la etapa de desarrollo, que tiene #[strong una importancia particular en relacin directa a su especie y a los requerimientos de productos demandados en un nicho de mercado]. Est relacionada directamente con el aumento lineal del cuerpo o peso corporal en funcin de la edad en especial cuando son jvenes, pero en cumplimento del control gentico, heredado para la especie y raza. Por otra parte, #[strong influyen factores externos tales como alimentacin, clima, sexo y estado de salud de los animales, entre otros]. El incremento del peso vivo es secuencial debido al desarrollo de la masa muscular. 

      .row(titulo="3. Reproduccin")
        .col-md-4
          img.img-a.mb-5(src="@/assets/curso/tema_1/img_37.png")
        .col-md-7.mb-4.mb-md-0
          p La tercera etapa del ciclo de vida de los semovientes es la etapa reproductiva. #[strong Esta depende de la madurez sexual, la cual se logra a medida que se acerca esta madurez y el proceso reproductivo persigue como finalidad la perpetuacin de la especie]. Se inicia con la produccin de gametos, contina con la fecundacin, implantacin del cigoto, gestacin, lactancia y finaliza con el destete. Proceso propio de cada especie. 

      .row(titulo="4. Muerte")
        .col-md-4
          img.img-a.mb-5(src="@/assets/curso/tema_1/img_38.png")
        .col-md-7.mb-4.mb-md-0
          p Como finalidad del proceso productivo para #[strong la alimentacin humana, la muerte de animales de una especie determinada es la culminacin del ciclo de vida del semoviente, en el cual se ha conseguido alcanzar las caractersticas ideales y parmetros tcnicos previstos], logrando obtener el mayor desarrollo corporal en el menor tiempo y a la edad prevista para este fin.

    .titulo-segundo
      #t_1_10_2.h5 1.10.2 Tcnicas de manejo

    p.mb-5 Cada especie de animales con fines econmicos, tiene un principio bsico de manejo y debe ser coherente con el comportamiento o etologa del animal. #[strong Las tcnicas de manejo utilizadas en una explotacin pecuaria promueven el bienestar animal, procuran evitar su excitacin, ansiedad, maltrato y realizan buenas prcticas de manejo]. Ya que despus de entrar en pnico o estrs un semoviente, pueden pasar alrededor de 30 minutos para que el animal se tranquilice y normalice su ritmo cardiaco. 

    p.text_tamano.mb-5 #[strong.color_text Los animales calmados permiten la realizacin de todo tipo de actividades sin dificultad;] #[strong adems que se desplazan con mayor facilidad y estn menos dispuestos a agruparse. ]

    p.mb-5 Lo ideal es que se sientan en confort. #[strong El manejo seguro de los animales comienza conociendo el comportamiento innato del animal y por este motivo, se deben ensear las tcnicas seguras de manejo a los funcionarios para usarlas permanentemente]. En general, dentro de una explotacin pecuaria los trabajadores deben hacer movimientos lentos, tranquilos y suaves alrededor de los animales, evitar gritos y maltratos. Se recomienda en casos extremos la utilizacin de una descarga elctrica suave sin tener que golpearlo o torcerle la cola. 


    .row.m-0.align-items-center.justify-content-between.mb-5
      .col-lg-5.mb-4.mb-lg-0
        img.img-a(src='@/assets/curso/tema_1/img_39.png')
        p Tcnica de manejo bovino.
      .col-lg-7
        .cajon--lavanda.p-5.bloque-txt Los semovientes cuando son aislados de los dems de su manada, por lo general se agitan. Si esto ocurre se debe llevar con otros de su misma especie para minimizar su estrs. 
          |
          strong Cualquier alteracin en las condiciones de confort de una especie influye en la productividad de la empresa. 


